@import "../../variables.scss";

.banner {
    height: 100vh;
    // margin-bottom: 70px;
    position: relative;
    overflow: hidden;

    &__dark-layer {
        width: 53%;
        height: 100%;
        position: relative;
        top: 0;
        left: 47%;
        background-color: $dark-bg;
        background-repeat: no-repeat;
        background-position: center right;
        background-size: auto 80%;
        clip-path: polygon(30% 0%, 100% 0%, 100% 100%, 0% 100%);
    }

    &__info {
        position: absolute;
        color: #fff;
        width: fit-content;
        text-align: end;
        background-color: $dark-bg;
        z-index: 10;
    }

    &__info--main {
        top: 40vh;
        right: 8vw;
        font-size: 34px;
        text-shadow: 5px 5px 3px #000;
    }

    &__info--details {
        bottom: 2vh;
        right: 50px;
        font-size: 21px;
    }

    &__action {
        position: relative;
        width: 250px;
        height: 50px;
        color: #fff;
        font-size: 15px;
        border: 2px solid #fff;
        transform: skewX(-15deg);
        // border: none;
        // box-shadow:  3px 3px 16px 0px rgba(0, 0, 0, 1);
        outline-style: none;
        background: transparent;
        cursor: pointer;

        &::before {
            content: "";
            position: absolute;
            top: -2px;
            left: -2px;
            width: 0px;
            height: 50px;
            background-color: transparent;
            border: 2px solid $juicy-orange;
            -webkit-transition: width 0.3s ease-out 0.3s;
            -moz-transition: width .3s ease-out 0.3s;
            -o-transition: width .3s ease-out 0.3s;
            transition: width .3s ease-out 0.3s;
        }

        &::after {
            content: "";
            position: absolute;
            top: -2px;
            left: -15px;
            border: 2px solid $juicy-orange;
            width: 0px;
            height: 50px;
        }

    }

    &__action:hover::before {
        width: 250px;
    }
}