@import "../../variables.scss";

.footer {
    padding-top: 50px;
    padding-bottom: 50px;
    background: $dark-bg;
    overflow: hidden;

    &__bg-layer {
        position: relative;
        background-color: #20242c;
        z-index: 0;
    }

    &__content {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 0 25px 0 0;
    }

    &__company {
        width: 30%;
        color: #fff;
        text-align: justify;
        background-color: $dark-bg;

        &-logo {
            width: 200px;
        }

        &-fb {
            font-size: 30px;
            color: $juicy-green;

            &:hover {
                color: $juicy-orange;
            }
        }
    }


    &__contacts {
        width: 30%;
        height: 300px;
        color: #fff;
        // background-color: $dark-bg;
    }

    &__schedule {
        margin-bottom: 20px;

        .fa-clock {
            font-size: 25px;
            margin-right: 20px;
            color: $juicy-green;
            transition: color 0.1s;
        }

        span {
            font-style: normal;
            font-size: 16px;
        }

        span:last-of-type {
            text-shadow: 0 0 5px $neon-green;
        }
    }

    &__schedule:hover .fa-clock{
        color: $juicy-orange;
    }

    .schedule-indicator {
        display: inline-block;
        margin: 0 3px 0 10px;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        box-shadow: 0 0 5px $neon-green;
    }


    &__address {
        margin-bottom: 20px;
        .fa-map-marker-alt {
            font-size: 25px;
            margin-left: 3px;
            margin-right: 20px;
            color: $juicy-green;
            transition: color 0.1s;
        }

        a {
            text-decoration-line: underline;
            font-size: 14px; 
            padding-left: 10px;
        }

        span {
            font-style: normal;
            font-size: 16px;
        }
    }

    &__address:hover .fa-map-marker-alt{
        color: $juicy-orange;
    }

    &__email {
        margin-bottom: 20px;
        .fa-envelope {
            font-size: 25px;
            margin-right: 20px;
            color: $juicy-green;
            transition: color 0.1s;
        }

        a {
            font-style: normal;
            font-size: 18px;
            font-weight: bolder;
        }
    }

    &__email:hover .fa-envelope{
        color: $juicy-orange;
    }

    &__phone {
        margin-bottom: 20px;
        display: flex;
        align-items: center;

        .fa-phone-volume{
            transform: rotate(-45deg);
            font-size: 25px;
            margin-left: 4px;
            margin-right: 21px;
            color: $juicy-green;
            transition: color 0.1s;
        }

        a {
            font-style: normal;
            font-size: 18px;
            font-weight: bolder;
        }
    }

    &__phone:hover .fa-phone-volume{
        color: $juicy-orange;
    }

    &__links {
        width: auto;
        height: 75px;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: transparent;

        a {
            width: fit-content;
            text-decoration: underline;
            transition: color 0.3s;
            background: $dark-bg;
        }

        a:hover {
            color: $juicy-orange
        }
    }
}