@import "../../variables.scss";

.company-basics {
    position: relative;
    background-color: $dark-bg;
    // box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 1);

    &__list {
        // margin-top: -70px;
        display: flex;
        justify-content: center;
        padding: 20px 0;
        // background: transparent;
    }

    &__square {
        position: absolute;
        bottom: -15px;
        left: calc(50% - 15px);
        width: 30px;
        height: 30px;
        transform: rotate(45deg);
        background-color: $dark-bg;
    }

    
}