@import '../../variables.scss';

$interval: 10px;
@mixin hover-state {
    color: $juicy-green;
    // font-weight: 400;
}



.top-bar {
    width: 100%;
    height: 80px;
    max-width: 1600px;
    position: fixed;
    z-index: 11111;
    top: 0;
    color: #fff;
    background-color: $dark-bg;

    &--scroll {
        -webkit-box-shadow: 0px 2px 5px 0px #040404;
        -moz-box-shadow: 0px 2px 5px 0px #040404;
        box-shadow: 0px 2px 5px 0px #040404;
        opacity: 0.9;
    }

    &__content {
        width: 98%;
        // max-width: 1600px;
        margin: 0 auto;
        font-size: 16px;
    }

    &__wrapper {
        display: flex;
        align-content: center;
        justify-content: space-between;
        padding: 5px 0;
    }

    &__wrap-left {
        @include flex-left;
    }

    &__logo {
        width: 100px;
        padding: 0 10px;

        & img {
            width: 100%;
        }
    }

    &__address {
        padding-right: $interval;
        margin-right: $interval;
        border-right: 2px solid $text-color;
        font-style: normal;
        line-height: 16px;
        font-weight: 300;
    }

    &__email {
        padding-right: $interval;
        font-weight: 300;
        // font-weight: lighter;

        a {
            padding-left: 5px;
            line-height: 28px;
        }
    }

    &__email:hover {
        & a,  & svg {
            @include hover-state();
        }
    }

    &__phone {
        display: flex;
        align-items: center;
        padding-left: $interval;
        margin-left: $interval;
        border-left: 2px solid $text-color;

        .fa-phone-volume{
            transform: rotate(-45deg);
            font-size: 22px;
            margin-right: 10px;
            color: $juicy-green;
            -webkit-animation: ring 5s infinite ease-in-out;
            animation: ring 5s infinite ease-in-out;
        }

        a {
            font-style: normal;
            // font-size: 18px;
            // color: $juicy-green;
            font-weight: 600;
            transition: color 0.5s;
        }
    
        a:hover {
            color: $juicy-green;
        }
    }

    &__wrap-right {
        @include flex-right;
    }

    &__langs {
        padding-right: $interval;
        margin-right: $interval;
        border-right: 2px solid $text-color;
        line-height: 16px;
        cursor: pointer;
        font-weight: 300;
    }

    .lang-active {
        font-weight: 600;
        color: #fff;
    }

    &__lang:not(.lang-active) {
        &:hover {
            text-decoration: underline;
        }
    }

    &__socials {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: 3px 10px;
        border: 2px solid $juicy-green;
        // border: 2px solid #fff;
        color: #fff;
        font-size: 14px;
        background-color: transparent;
        transition: background-color 0.5s;

        &:hover {
            background-color: $juicy-green;
        }

        .fa-facebook {
            box-sizing: content-box;
            padding-left: 5px;
            margin-left: 5px;
            font-size: 18px;
            color: #fff;
            border-left: 2px solid $juicy-green;
            // border-left: 2px solid #fff;
        }
    } 
}

@keyframes ring {
    0% {
        -webkit-transform:rotate(-45deg) scale(1)
    }

    2% {
        -webkit-transform:rotate(-30deg) scale(1)
    }

    4% {
        -webkit-transform:rotate(-45deg) scale(1)
    }

    6% {
        -webkit-transform:rotate(-30deg) scale(1)
    }

    8% {
        -webkit-transform:rotate(-45deg) scale(1)
    }

    10% {
        -webkit-transform:rotate(-45deg) scale(1)
    }

    100% {
        -webkit-transform:rotate(-45deg) scale(1)
    }
}