@import "../../variables.scss";

.complex-systems {
    margin-bottom: 100px;

    &__header {
        position: relative;
        padding-bottom: 50px;
    }

    &__i {
        position: absolute;
        top: -45px;
        left: 20%;
        font-family: 'Poppins', sans-serif;
        -webkit-text-stroke: 1px rgba(214, 214, 214, 0.5);
        letter-spacing: 1px;
        line-height: 144px;
        font-weight: 900;
        font-size: 144px;
        text-transform: uppercase;
        color: transparent;
        user-select: none;
    }

    & .logo {
        width: -moz-fit-content;
        width: fit-content;
        margin-left: auto;
        margin-right: auto;
        top: 5px;
    }

    &__title {
        padding: 10px 0 20px;
        position: relative;
        z-index: 1;
        font-size: 36px;
        font-weight: 400;
        line-height: 40px;
        text-align: center;
    }

    &__cards {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
    }
}