@import "../../variables.scss";

.complex-systems-card__content {
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 340px;
    box-shadow: 9px 12px 38px 0px rgba(0, 0, 0, 0.4);
    overflow: hidden;
    transition: all 0.5s easy;
    -webkit-transition: all 0.5s ease;
     -moz-transition: all 0.5s easy;
     -o-transition: all 0.5s easy;
    transform: scale(1);

    &:hover {
        transform: scale(1.1);
    }
}

.complex-systems-card__header {
    position: relative;
    width: 100%;
    height: 220px;
    overflow: hidden;

   img {
    margin-left: -123px;
    margin-top: -36px;
   }
}

.complex-systems-card__price-badge {
    position: absolute;
    bottom: 0px;
    width: 100%;
    background-color: $juicy-orange;
    color: white;
    text-indent: 10px;
    line-height: 30px;
    font-weight: bold;
}

.complex-systems-card__power-badge {
    position: absolute;
    top: 0px;
    right: -1px;
    width: 100px;
    height: 75px;
    padding: 3px;
    background-color: white;
    color: $juicy-orange;
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    line-height: 65px;
    text-indent: 16px;
    font-size: 20px;
    font-weight: bold;
}

.complex-systems-card__body {
    padding: 10px;
}