@import "../../variables.scss";

.benefits {
    position: relative;

    &__i {
        position: absolute;
        width: 950px;
        overflow: hidden;
        left: calc(50% - 475px); //950px / 2
        font-family: 'Poppins', sans-serif;
        -webkit-text-stroke: 1px rgba(214, 214, 214, 0.5);
        letter-spacing: 1px;
        line-height: 144px;
        font-weight: 900;
        font-size: 144px;
        text-transform: uppercase;
        color: transparent;
        user-select: none;
    }

    & .logo {
        width: fit-content;
        margin-left: auto;
        margin-right: auto;
        top: 60px;
    }

    &__title {
        position: relative;
        top: 32px;
        padding: 20px 0;
        font-size: 36px;
        font-weight: 400;
        text-align: center;
    }

    &__grid {
        margin-top: 0px;
        padding: 70px 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__grid-item {
        position: relative;
        width: 370px;
        height: 262px;
        padding: 20px;
        margin-bottom: 70px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        background-color: #fff;
        transition: transform 0.3s ease-in;
        -webkit-transition: transform 0.3s ease-in, perspective 0.3s ease-in;
        box-shadow: -45px 12px 38px 0px rgba(0, 0, 0, 0.4);
        transform: skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
        -webkit-transform: skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
        font-style: italic;


        &:hover {
            transform: perspective(600) skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
            -webkit-transform: perspective(600) skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
        }

        &:before {
            content: "";
            position: absolute;
            left: -15px;
            top: -1px;
            width: 15px;
            height: 262px;
            background-color: $juicy-green;
            clip-path: polygon(0 0, 100% 100%, 100% 0);
        }

        &:after {
            content: "";
            position: absolute;
            top: -15px;
            left: -15px;
            width: 370px;
            height: 15px;
            background-color: $juicy-green;
            clip-path: polygon(0 0, 0% 100%, 100% 100%);
        }
    }

    &__grid-icon {
        width: 70px;
        height: 70px;
        fill: $juicy-orange;
    }

    &__grid-text {
        text-align: center;
    }


}

.benefits__grid-item:hover .benefits__grid-item::before{
    transform: rotate(20deg);
}